.landing-page {
    background-color: #0f172a;
    color: #0f172a;
}

.landing-hero {
    position: relative;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: #f8fafc;
    padding: 7rem 0 5rem;
    background-color: #0f172a; /* fallback if image fails */
}

.landing-hero__overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, rgba(15, 23, 42, 0.9), rgba(30, 64, 175, 0.75));
}

.hero-badge {
    letter-spacing: .08em;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.16) !important;
}

.hero-title {
    font-size: clamp(2.5rem, 4vw, 3.5rem);
    font-weight: 700;
    color: #ffffff;
}

.hero-typed {
    display: block;
    font-size: clamp(1.4rem, 2.5vw, 2rem);
    font-weight: 500;
    color: #e5e7eb;
}


.hero-buttons {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 0.75rem;
}

.hero-btn {
    border-radius: 999px !important;
    padding: 0.6rem 1.25rem !important;
    font-weight: 600;
    display: inline-flex;
    align-items: center;
    gap: 0.65rem;
    font-size: 0.95rem;
    box-shadow: 0 8px 20px rgba(15, 23, 42, 0.2);
    transition: transform 150ms ease, box-shadow 150ms ease, background 150ms ease;
}

.hero-btn__icon {
    font-size: 1rem;
}

.hero-btn.btn-outline-light {
    background: rgba(15, 23, 42, 0.2);
    border-color: rgba(248, 250, 252, 0.35);
    color: #e2e8f0;
}

.hero-btn:hover,
.hero-btn:focus-visible {
    transform: translateY(-2px);
    box-shadow: 0 14px 32px rgba(15, 23, 42, 0.28);
}

@media (max-width: 575px) {
    .hero-buttons {
        flex-wrap: wrap;
        justify-content: center;
    }

    .hero-btn {
        width: 100%;
        justify-content: center;
    }
}

.landing-summary {
    background: #ffffff;
}

.summary-card {
    border-radius: 1.25rem;
    padding: 0.75rem;
}

.summary-header {
    display: grid;
    gap: 0.35rem;
    margin-bottom: 1rem;
}

.summary-badge {
    width: fit-content;
    letter-spacing: 0.08em;
}

.summary-text {
    font-size: 1.05rem;
    color: #475569;
    margin-bottom: 1.5rem;
}

.summary-stats {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
    margin-bottom: 1.5rem;
}

.summary-stat {
    background: rgba(37, 99, 235, 0.12);
    border-radius: 1rem;
    padding: 0.65rem 1rem;
    display: grid;
    gap: 0.15rem;
    min-width: 140px;
}

.summary-stat__value {
    font-weight: 700;
    color: #0f172a;
}

.summary-stat__label {
    font-size: 0.85rem;
    color: #475569;
}

.summary-meta {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    font-weight: 600;
    color: #0f172a;
    margin-bottom: 1.5rem;
}

.summary-meta a {
    color: inherit;
    text-decoration: none;
}

.summary-meta a:hover,
.summary-meta a:focus-visible {
    color: #2563eb;
    text-decoration: underline;
}

.summary-actions {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
}

.summary-btn {
    border-radius: 999px;
    padding: 0.6rem 1.35rem;
    transition: transform 150ms ease, box-shadow 150ms ease;
}

.summary-btn:hover,
.summary-btn:focus-visible {
    transform: translateY(-2px);
    box-shadow: 0 12px 28px rgba(37, 99, 235, 0.25);
}

.landing-build {
    background: #f8fafc;
}

.build-card {
    border-radius: 1.25rem;
    padding: 0.85rem;
}

.build-header h2 {
    margin-top: 0.75rem;
    color: #0f172a;
}

.build-header p {
    margin-top: 0.75rem;
}

.build-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
}

.build-tile {
    display: flex;
    gap: 0.75rem;
    padding: 0.85rem 1rem;
    border: 1px solid rgba(15, 23, 42, 0.08);
    border-radius: 1rem;
    background: #f8fafc;
    box-shadow: 0 6px 16px rgba(15, 23, 42, 0.06);
    transition: transform 150ms ease, box-shadow 150ms ease, background 150ms ease;
}

.build-tile:hover,
.build-tile:focus-visible {
    transform: translateY(-2px);
    box-shadow: 0 12px 26px rgba(15, 23, 42, 0.12);
    background: #e2e8f0;
}

.build-tile__icon {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    display: grid;
    place-items: center;
    background: rgba(37, 99, 235, 0.12);
    color: #1d4ed8;
    font-size: 1.2rem;
}

.build-tile__content {
    display: grid;
    gap: 0.25rem;
}

.build-tile__content strong {
    color: #0f172a;
}

.build-tile__content span {
    color: #475569;
    font-size: 0.95rem;
}

.build-highlights {
    margin: 0;
    padding-left: 1.1rem;
    color: #475569;
}

.build-highlights li {
    margin-bottom: 0.5rem;
}

.text-white-70 {
    color: rgba(255, 255, 255, 0.85) !important;
}

.hero-skill-card {
    border-radius: 1.5rem;
    background: rgba(255, 255, 255, 0.98);
}

.skill-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 1rem;
}

.skill-icon-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 0.85rem;
    margin-top: 1.25rem;
}

.skill-icon-tile {
    background: rgba(15, 23, 42, 0.05);
    border-radius: 1rem;
    padding: 0.85rem;
    display: flex;
    align-items: center;
    gap: 0.7rem;
    transition: transform 150ms ease, box-shadow 150ms ease;
}

.skill-icon-tile:hover {
    transform: translateY(-2px);
    box-shadow: 0 12px 30px rgba(15, 23, 42, 0.15);
}

.skill-icon-tile__icon {
    width: 38px;
    height: 38px;
    border-radius: 12px;
    display: grid;
    place-items: center;
    background: rgba(37, 99, 235, 0.12);
    color: #1d4ed8;
    font-size: 1.25rem;
}

.skill-icon-tile span:last-child {
    color: #0b1120;
    font-weight: 600;
}

.hero-experience-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    gap: 0.65rem;
}

.hero-experience-list__title {
    font-weight: 600;
    color: #1e293b;
}

.hero-experience-list__period {
    font-size: 0.85rem;
    color: #64748b;
}

.demo-credentials {
    padding: 1rem;
    border-radius: 1rem;
    background: rgba(37, 99, 235, 0.08);
}

.credential-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.95rem;
    padding: 0.25rem 0;
}

.credential-row strong {
    font-family: 'Roboto Mono', monospace;
    color: #0f172a;
}

.landing-social a {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background: rgba(37, 99, 235, 0.12);
    display: grid;
    place-items: center;
    color: #1d4ed8;
    transition: transform 150ms ease, background 150ms ease;
    font-size: 1.15rem;
}

.landing-social a:hover {
    transform: translateY(-2px);
    background: rgba(37, 99, 235, 0.25);
}

.landing-skills {
    background: #0f172a;
}

.section-title {
    font-size: clamp(1.5rem, 3vw, 2.2rem);
    font-weight: 700;
    color: #e2e8f0;
}


.toolkit-heading {
    display: grid;
    gap: 0.75rem;
}

.toolkit-badge {
    width: fit-content;
    letter-spacing: 0.08em;
}

.toolkit-nav {
    display: flex;
    flex-direction: column;
    gap: 0.55rem;
    margin-top: 1rem;
}

.toolkit-nav__btn {
    border: 1px solid rgba(255, 255, 255, 0.25);
    background: transparent;
    color: #e2e8f0;
    border-radius: 999px;
    padding: 0.45rem 1.1rem;
    font-weight: 600;
    text-align: left;
    transition: background 150ms ease, color 150ms ease, border-color 150ms ease;
    cursor: pointer;
}

.toolkit-nav__btn span {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
}

.toolkit-nav__icon {
    font-size: 1rem;
}

.toolkit-nav__btn:hover,
.toolkit-nav__btn--active {
    background: rgba(248, 250, 252, 0.2);
    color: #0b1120;
    border-color: rgba(248, 250, 252, 0.4);
}

.toolkit-nav__btn--active .toolkit-nav__icon,
.toolkit-nav__btn:hover .toolkit-nav__icon {
    color: #0b1120;
}

.toolkit-card {
    border-radius: 1.25rem;
    background: #ffffff;
}

.toolkit-label {
    display: block;
    text-transform: uppercase;
    font-size: 0.65rem;
    letter-spacing: 0.08em;
    color: #94a3b8;
}

.toolkit-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
}

.toolkit-grid__item {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.85rem;
    border: 1px solid rgba(15, 23, 42, 0.08);
    border-radius: 1rem;
    box-shadow: 0 6px 16px rgba(15, 23, 42, 0.06);
    background: #f8fafc;
    transition: transform 150ms ease, box-shadow 150ms ease, background 150ms ease;
}

.toolkit-grid__icon {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    display: grid;
    place-items: center;
    background: rgba(37, 99, 235, 0.12);
    color: #1d4ed8;
    font-size: 1.2rem;
}

.toolkit-grid__label {
    font-weight: 600;
    color: #0b1120;
}

.toolkit-grid__item:hover,
.toolkit-grid__item:focus-visible {
    transform: translateY(-2px);
    box-shadow: 0 12px 26px rgba(15, 23, 42, 0.14);
    background: #e2e8f0;
}

.experience-heading h2 {
    margin-top: 0.75rem;
    color: #0f172a;
}

.experience-heading p {
    max-width: 38rem;
    margin: 0.75rem auto 0;
}

.experience-accordion {
    display: grid;
    gap: 0.85rem;
}

.experience-item {
    border: 1px solid rgba(15, 23, 42, 0.08);
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0 8px 18px rgba(15, 23, 42, 0.08);
    background: #ffffff;
    transition: box-shadow 150ms ease, border-color 150ms ease;
}

.experience-item--open {
    border-color: rgba(37, 99, 235, 0.25);
    box-shadow: 0 12px 26px rgba(15, 23, 42, 0.12);
}

.experience-toggle {
    width: 100%;
    border: none;
    background: rgba(248, 250, 252, 0.85);
    padding: 1rem 1.25rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    cursor: pointer;
    transition: background 150ms ease, color 150ms ease;
}

.experience-toggle:focus-visible {
    outline: 3px solid rgba(37, 99, 235, 0.45);
    outline-offset: 2px;
}

.experience-item--open .experience-toggle {
    background: #ffffff;
}

.experience-icon {
    font-size: 1rem;
    transition: transform 150ms ease;
}

.experience-item--open .experience-icon {
    transform: rotate(180deg);
}

.experience-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 1rem;
}

.experience-header__title {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
}

.experience-role {
    font-weight: 700;
    color: #0f172a;
}

.experience-company {
    color: #475569;
    font-size: 0.95rem;
}

.experience-meta {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.experience-pill {
    display: inline-flex;
    align-items: center;
    padding: 0.35rem 0.75rem;
    border-radius: 999px;
    background: rgba(37, 99, 235, 0.12);
    color: #1d4ed8;
    font-weight: 600;
    font-size: 0.85rem;
}

.experience-list {
    margin: 0;
    padding-left: 1.1rem;
    color: #475569;
}

.experience-list li {
    margin-bottom: 0.55rem;
}

.experience-body {
    max-height: 0;
    overflow: hidden;
    transition: max-height 200ms ease;
    padding: 0 1.25rem;
}

.experience-body--open {
    padding: 1rem 1.25rem 1.25rem;
    max-height: 600px;
}

.landing-projects {
    background: #ffffff;
}

.project-card {
    border-radius: 1rem;
}

.project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 0.35rem;
}

.project-chip {
    background: rgba(15,23,42,.06);
    color: #0f172a;
    border-radius: 999px;
    padding: .25rem .6rem;
    font-size: .85rem;
    font-weight: 600;
}

.project-link {
    color: #2563eb;
    font-weight: 700;
    text-decoration: none;
}

.project-link:hover { text-decoration: underline; }

.landing-personal { background: #f8fafc; }
.personal-card { border-radius: 1rem; background: #ffffff; }
.personal-list { margin: 0; padding-left: 1.1rem; color: #475569; }
.interest-chips { display: flex; flex-wrap: wrap; gap: .5rem; }
.interest-chip { background: rgba(15,23,42,.06); color: #0f172a; font-weight: 600; border-radius: 999px; padding: .35rem .75rem; }
.contact-links .btn { border-radius: 999px; }
.personal-intro { color: #475569; }
.personal-grid { display: grid; gap: 1rem; grid-template-columns: repeat(auto-fit, minmax(220px,1fr)); }
.personal-block h6 { font-weight: 700; color: #0f172a; margin-bottom: .5rem; }

.contact-links .btn {
    transition: transform 150ms ease, box-shadow 150ms ease;
}

.contact-links .btn:hover,
.contact-links .btn:focus-visible {
    transform: translateY(-2px);
    box-shadow: 0 12px 24px rgba(37, 99, 235, 0.25);
}

.landing-cta {
    background: linear-gradient(135deg, rgba(30, 64, 175, 0.12), rgba(37, 99, 235, 0.16));
}

.cta-title {
    font-weight: 700;
    color: #0f172a;
}

@media (max-width: 991px) {
    .landing-hero {
        padding-top: 6rem;
    }
}

@media (max-width: 575px) {
    .hero-buttons {
        flex-direction: column;
    }

    .demo-credentials,
    .overview-card {
        font-size: 0.9rem;
    }
}
